<template>
  <div class="detail" v-if="detail._id">
    <p class="xiqi"></p>
    <div class="nav">
      <ul>
        <li class="l-btn" @click=$router.go(-1)></li>
      </ul>
    </div>
    <div class="content">
      <div class="header clear"><h2><img width="50" :src=" baseUrl + detail.detail.auth_icon" alt=""/></h2><p>{{detail.detail.auth}}</p></div>
      <div class="cont">
        <h3>{{detail.title}}</h3>
        <div class="time"><p>{{detail.time | date}} / {{ 8 | fillzero }}<span><img src="../assets/img/zan.png" alt=""/></span></p></div>
        <div class="text-box" v-html="detail.detail.content"></div>
      </div>
    </div>
    <div class="foot-btn">
      <ul>
        <li class="say"><a href="javascript:;">
          <i></i><span>0</span>
        </a></li>
        <li class="zan"><a href="javascript:;">
          <i></i><span>0</span>
        </a></li>
        <li class="xing"><a href="javascript:;">
          <i><img src="../assets/img/xing.png" alt=""/></i>
        </a></li>
        <li class="fx"><a href="javascript:;">
          <i><img src="../assets/img/fx.png" alt=""/></i>
        </a></li>
      </ul>
    </div>
  </div>
  
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      detail: {}
    };
  },
  mounted() {
    this.$root.loding = true
    axios({
      url: "/api/" + this.$route.query.dataName + "/" + this.$route.params.id
    }).then(
      res => {
        setTimeout(()=>{
           this.detail = res.data.data
        this.$root.loding = false

        },2000)
        
       
        }

      );
  }
};
</script>
<style scoped>
.xiqi{
  text-align: center;
}

</style>