<script>
import axios from 'axios'
export default {
  data() {
    return {
      user:{}
    }
  },
  beforeRouteEnter (to, from, next) {
    // 
    axios({
      url:'api/user',
      method:"post"

    }).then(
      res => res.data.err === 0 ? next(_this=>_this.user=res.data.data) : next("/login")

    )

  },
  methods: {
    logout(){
      axios({
        url:'/api/logout',
        method:'delete'
      }).then(
         res => res.data.err === 0 ? this.$router.push('/home') : null 

      )
    }
  },
}
</script>
