<template>
    <div>
        <silding></silding>
        <list :list="list" dataName="follow"></list>
        
    </div>
</template>
<script>
import silding from './silding'
import list from './list'
import axios from 'axios'
export default {
    data() {
        return {
            list:[]
        }
    },
    components:{
        silding,list
    },
    mounted(){
        axios({
            url:'/api/follow',
            params:{_page:1,_limit:8}
        }).then(
            res=>this.list = res.data.data
        )
    }
}
</script>