<template>
  <div id="app">
    <!-- <img alt="Vue logo" src="./assets/logo.png">
    <HelloWorld msg="Welcome to Your Vue.js App"/> -->
    <loding v-if="$root.loding"></loding>
    <Header v-if="$root.header"></Header>
    <Nav v-if="$root.Nav"></Nav>
    
      
    <transition enter-active-class = "animated fadeIn" leave-active-class="animated fadeOut">
    <router-view></router-view></transition>
    <Footer v-if="$root.footer"></Footer>
     

  </div>
</template>

<script>
// import HelloWorld from './components/HelloWorld.vue'
import Header from './components/Header'
import Nav from './components/Nav'
import Footer from './components/Footer'
import loding from './pages/loding'


// import mainl from './pages/mainl'
export default {
  name: 'app',
  components: {
    // HelloWorld
    Header,Nav,Footer,loding
  },
  watch:{
    $route:{
      handler(nextRoute){
        
        let path = nextRoute.path;
        if(/home|follow|column/.test(path)){
          this.$root.header=true;
          this.$root.footer=true;
           this.$root.Nav = true;
        }
        if(/user/.test(path)){
          this.$root.header=true;
          this.$root.footer=true;
           this.$root.Nav = false;
        }
        if(/login|reg|Nav|detail/.test(path)){
          this.$root.header=true;
          this.$root.Nav = false;
          this.$root.footer=false;
        }
      },
      immediate:true
    }
  }

}
</script>

