<template>
    <div class="c">
        <ul>
            <li v-for="(item) of c" :key="item._id">
                <div class="top"><img :src="baseUrl + item.author_face"/><span>{{item.title}}</span></div><div class="time">{{item.time | date}}</div>
                 <router-link :to="'/cd/' + item._id "><div class="img"><img :src="baseUrl + item.img"/></div>
              <p>{{item.content}}</p></router-link> 
            </li>
        </ul>
        
    </div>
</template>
<style scoped>

.c ul li{
    
    
    margin-top:20px;
}
.c ul li .top{
    float: left;
    margin-bottom:20px;
}
.c ul li .top img{
    width: 50px;
    height: 50px;
    margin-left:10px;
}
.c ul li .time{
    float:right;
    margin-top:10px;
     margin-bottom:20px;
     margin-right: 10px
}
.c ul li .img img{
    width:350px;
   
    
}
.c ul li p{
    font:20px/30px "";
    text-indent: 1em;
    color: dimgray;
    margin-top:10px;
}

</style>
<script>
import axios from 'axios'
export default {
    data() {
        return {
            c:[]
        }
    },
    mounted(){
        axios({
            url:'/api/home',
            params:{_page:1,_limit:10},
        }).then(
            res=>this.c = res.data.data
        )
    }
}
</script>