  <van-cell-group style="text-align:center">
    <van-field
      v-model="username"
      required
      clearable
      label=""
      right-icon="question-o"
      placeholder=""
      @click-right-icon="$toast('question')"
     
    />

    <van-field v-model="password" type="password" label="" placeholder="" required   style="margin-bottom:30px"/>
    
    <router-link to="/login" class="a"><van-button type="primary" size="large" @click="login" style="margin-bottom:0.2px"></van-button></router-link>
    <router-link to="/reg" class="a"><van-button type="primary" size="large"></van-button></router-link>
   
  
  </van-cell-group>
  
</template>
<script>
import axios from "axios";

export default {
  data() {
    return {
      username: "",
      password: "",
      errmsg: "",
      value: ""
    };
  },
  methods: {
    login() {
      axios({
        url: "/api/login",
        method: "post",
        data: {
          username: this.username,
          password: this.password
        }
      }).then(res =>
        res.data.err === 0
          ? this.$router.push("/user")
          : (this.errmsg = res.data.msg)
      );
    }
  }
};
</script>
