    <h1 id="hh" @click="selectIcon"></h1>
    <input type="file" id="icon">
    <div class="login-box">
      <p class="lsolid"></p>
      <div class="login">
        <router-link :to="{path:'/login'}" class="a"></router-link>
        <span></span>
        <router-link to="/reg" class="a"></router-link>
      </div>
      <p class="rsolid"></p>
    </div>
    <ul>
      <li class="lifirst">
        <input type="text" v-model="username" />
        <span></span>
      </li>
