      <li>{{errmsg}}</li>
    </ul>
    <div class="footbox">
      <input type="button" value=" " class="login-btn" @click="reg"/>
     
    </div> -->
    <van-cell-group style="text-align:center">
    <van-field
      v-model="username"
      required
      clearable
      label=""
      right-icon="question-o"
      placeholder=""
      @click-right-icon="$toast('question')"
     
    />

    <van-field v-model="password" type="password" label="" placeholder="" required   style="margin-bottom:30px"/>
    <input type="file" label="" id="icon" value="">
    
    <router-link to="/login" class="a"><van-button type="primary" size="large" style="margin-bottom:1px"></van-button></router-link>
    <router-link to="/reg" class="a"><van-button type="primary" size="large" @click="reg"></van-button></router-link>
   
  
  </van-cell-group>
  
  </div>
</template>
<script>
import axios from 'axios'
export default {
    data() {
        return {
            username:'',
            password:'',
            errmsg:'',
            
        }
    },
    methods: {
        selectIcon(){
            let input = document.getElementById('icon');
            input.click()

        },
        reg(){
            let formData = new FormData()
            formData.append("username",this.username)
            formData.append('password',this.password)
            let input = document.getElementById("icon")
            formData.append('icon',input.files[0])
            axios({
                url:'/api/reg',
                method:'post',
                data:formData
            }).then(
                res=>res.data.err === 0 ? this.$router.push('/login') : this.errmsg = res.data.msg

            )

        }
    },

    
}
</script>
<style scoped>
    .content{
     text-align: center
 }
 .content .login .a{
     margin-left: 30px;
 }
 .login-btn{
     margin-top: 30px;
 }
 .lifirst{
     margin-bottom: 20px;
 }
 ul li span{
     margin-left: 10px;
 }
</style>