<template>
  <div>
    <h3></h3>
    <div id="allmap" style="height:400px"></div>
    <p></p>
  </div>
</template>
<script>
console.log(BMap);
export default {
  mounted() {
    //   console.log(1);
    // API
    var map = new BMap.Map("allmap");
    var point = new BMap.Point(121.48,31.41);
    map.centerAndZoom(point, 15);
    var marker = new BMap.Marker(point); // 
    map.addOverlay(marker); // 
    marker.setAnimation(BMAP_ANIMATION_BOUNCE);
    //
    var local = new BMap.LocalSearch(map, {
      renderOptions: { map: map }
    });
    local.search("");
    
  }
};
</script>
<style scoped>
body,
html {
  width: 100%;
  height: 100%;
  margin: 0;
  font-family: "";
}
#allmap {
  width: 100%;
  height: 500px;
}
p {
  margin-left: 5px;
  font-size: 14px;
}
</style>

