<template>
    <div class="cd">
        <div class="img"><img :src="baseUrl + list.img"/></div>
        <p>{{list.content}}</p>
        
    </div>
</template>
<style scoped>

.img img{
    width:350px;
   
    
}
 p{
    font:20px/30px "";
    text-indent: 1em;
    color: dimgray;
    margin-top:10px;
}
</style>
<script>
import axios from 'axios'
export default {
   
    // props: ['index'],
    data() {
        return {
            list:{}
            
        }
    },
    mounted(){
        axios({
            url:"/api/home/"  + this.$route.params.id+"?dataName=home",
            // params:{_page:1,_limit:10},
        }).then(
            res=>this.list = res.data.data
        )
    }
}
</script>