<template>
    <div class="homedetail">
        <button @click="go"></button>
        <h3>{{homedatail.content}}</h3>
        <div class="img"><img :src="baseUrl + homedatail.img"/></div>
        <div class="title">{{homedatail.title}}</div>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    data() {
        return {
            homedatail:{}
        }
    },
    methods: {
        go(){
            this.$router.go(-1)
        }
    },
    mounted(){
        axios({
            url:"/api/column/"+ this.$route.params.id+"?dataName=column"

        }).then(
            res=>this.homedatail = res.data.data
        )
      

    }
}
</script>
<style scoped>
button{
    width: 50px;
    height: 30px;
    /* font:20px/30px ""; */
    text-align: center

}
.img img{
   height: 300px;
   width: 338px;
   margin-bottom:10px;
}
.title{
    font: 15px/30px "";
    text-indent:2em;
    
}
h3{
    text-align: center
}

</style>